import Vue from 'vue'
import App from './App.vue'
import router from './router'
import VueTouch from 'vue-touch'

Vue.config.productionTip = false
Vue.use(VueTouch, { name: 'v-touch' })
VueTouch.config.swipe = {
  threshold: 100 // 手指左右滑动距离
}
new Vue({
  router,
  render: h => h(App)
}).$mount('#app')
