<template>
  <div class="pc">
    <div class="item_list" v-show="isPc">
      <ul>
          <li v-for="(item, index) in list" :key="index">
            <span>{{ index+1 }}</span>
            <label>{{ item.text }}</label>
            <slot name="item" :item="item"></slot>
          </li>
      </ul>
    </div>
  </div>
</template>

<script>

export default {
  props: {
    isPc: Boolean,
    list: Array
  }
}

</script>
<style lang='scss' scoped>
button{
  margin-bottom: 20px;
  margin-top: -40px;
}
ul li{
  & a{
    text-decoration: none;
    color: #fff;
    cursor: pointer;
    transition: color 1s;
  }
  &:hover{
    .first{
      color: rgb(134, 23, 23);
    }
    .second{
      color: rgb(18, 114, 18);
    }
  }
}
</style>
