<template>
  <div id="phone">
      <div class="item_list_phone" v-show="isPhone">
        <v-touch
          v-for="(item, index) in list"
          :key="index"
          @swipeleft="$emit('swipeleft',item.id)"
          @swiperight="$emit('swiperight',item.id)"
          class="wrapper"
        >
          <div
            class="listItem"
          >
            <p>{{ item.text }}</p>
          </div>
        </v-touch>
      </div>
  </div>
</template>

<script>
export default {
  props: {
    isPhone: Boolean,
    list: Array
  }
}

</script>
<style lang='scss' scoped>
.item_list_phone{
  width: 100%;
  height: 100%;
  text-align: center;
}
.listItem {
  font-family: 楷体;
  border-bottom: 1px solid #b1aeae;
  padding-top: 20px;
  padding-bottom: 20px;
  p{
    margin: 0;
  }
}
.wrapper{
  // 由于默认touch-action是none，禁止了用户的所有手势操作
  // 所以加入此属性，就可以使用上下滑动
  touch-action: pan-y!important;
}
</style>
